.post {
    height: 300px;
    border: 1px solid black;
    border-radius: 15px;
    overflow: auto;
    transition: .25s all;
    position: relative;
    top: 0;
}

.post:hover {
    top: -5px;
    box-shadow: 2px 2px 6px 2px gray;
}

.id {
    background-color: #212121;
    padding: 10px 0;
    color: white;
    margin:0;
    border-bottom: 2px solid #61DAFB;
}

.title {
    color: gray;
}


