.custom_card {
    border: 1px solid gray;
    border-radius: 15px;
    height: 500px;
    padding:0;
    cursor: pointer;
    transition: .25s all;
}

.div_imagen {
    height: 230px;
}

.div_imagen img{
    object-fit: cover;
    object-position: center;
} 

#title {
    color: white;
    background-color: #212121;
    padding: 10px 0;
    margin: 0;
    letter-spacing: 1px;
    transition: .25s all;
    font-style: italic;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
}

#description {
    overflow: auto;
    max-height: 200px;
    text-align: justify;
}


.custom_card:hover {
    transform: scale(1.03);
    box-shadow: 0 2px 6px 2px gray;
}

.custom_card:hover #title {
    border-bottom: 2px solid  #212121;
    color: #212121;
    background-color: #61DAFB;
}

#description::-webkit-scrollbar {
    width: 5px;
}

#description::-webkit-scrollbar-thumb {
    background:  #61DAFB; 
    border-radius: 15px;
    border: 1px solid gray;
}

#description::-webkit-scrollbar-thumb:hover {
    background:  #47b2cf; 
}

#description::-webkit-scrollbar-track {
    border-top-right-radius: 15px;
    border-radius: 15px;
}

.boton_modal {
    background:  #212121 ;
    border: 1px solid #61DAFB ;
    position: absolute ;
    left: -43px ;
}

.boton_modal:hover {
    background:  white ;
    border: 1px solid #61DAFB ;
}

.boton_modal:hover .icono {
    color:  #61DAFB;
}

