h1,h2,h3,h4,h5,h6,p {
    margin-bottom: 5px;
    margin-top: 0;
}

.div_padre {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    border: 1px solid black ;
    border-radius: 15px;
    margin-bottom: 2px;
    transition: .28s ease-in-out all;
    color: white;
    width: 100%;
    position: relative;
}

.line> p {
    text-decoration: line-through;
}


/*   ---------- Default ---------- */
    .default {
        transition: .25s ease-in-out all;
        z-index: 100;
        background: linear-gradient(45deg,rgba(0, 0, 255, 0.596), rgba(11, 169, 197, 0.705));
    }

    .default:hover {
        /* background: linear-gradient(345deg,rgba(0, 0, 255, 0.596)*/
        background-position: 50px;
    }
/* ---------------------------------- */


/*  ---------- Done ---------- */
    .done {
        background: linear-gradient(45deg, rgba(11, 169, 197, 0.705), rgb(0, 255, 13));
        background-position: 100%;
        transition: .28s ease-in-out all;
        /* background: linear-gradient(45deg,rgba(0, 0, 255, 0.596), rgba(11, 169, 197, 0.705)); */

    }

    .done .div_hijo p, .done .div_hijo h3 {
        text-decoration: line-through;
    }

    .done:hover {
        background-color: rgba(0, 128, 0, 0.856);
    }

/* ----------------------------------- */


.div_hijo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 70%;
    max-height: 100px;
    overflow: auto;
}



.ex {
    background-color: rgb(255, 0, 0);
    padding: 15px;
    border-radius: 50%;
    color: white;
    border: none;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    height: 25px;
    width: 25px;
    align-self: center center;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    transition: .25s all;
}

.ex:hover {
    transform: scale(1.1);
}

