form {
    background: linear-gradient(350deg, rgba(0, 0, 255, 0.596), rgba(11, 169, 197, 0.705));
    padding: 10px;
    border: 1px solid black;
    border-radius: 15px;
}

form h1 {
    color: white;
}

input[type="submit"] {
    border: 1px solid gray;
    text-decoration: none;
    padding: 10px;
    background-color: transparent;
    font-weight: bold;
    color: white;
    transition: .25s all;
    border-radius: 0px;
}

input[type="submit"]:hover  {
    
    border-radius: 15px;
}

.error {
    color: white;
    padding: 10px;
    border: 1px solid red;
    animation: animarError .7s ease-in-out forwards;
}

.esconderNota {
    border: none;
    color: red;
    text-decoration: none;
    background-color: inherit;
}

.esconderNota:focus {
    outline: none;
}

.tareas_completadas {
    color: green;
    animation: entrada 1s ease-in-out forwards;
    position: relative;
    left: -100%;
    display: none;
}

@keyframes entrada {
    from{
        left: -100%;
    }

    to{
        left: 0;
    }
}



@keyframes animarError {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
