nav {
    background-color: #212121;
    padding: 20px;
    height: 70px;
} 

nav .link {
    text-decoration: none;
    color: white;
}

.link::after {
    content: "";
    width: 0;
    height:3px;
    background-color: orange;
    position: absolute;
    left: 0;
    bottom: -23px;
    opacity: 1;
    transition: all .25s;
}

nav .link:hover{
    color: #61DAFB;
}

nav .link:hover::after {
    animation: animatingHover .25s ease-out forwards;
} 

img {
    width: 33px;
}

.spa {
    color: white;
}


@keyframes animatingHover {

    100% {
        opacity: 1;
        width: 100%;
    }


}
